body {
  position: relative;
  font-family: Poppins, sans-serif;
  letter-spacing: 2px;
  line-height: 1.5em;
  margin: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  letter-spacing: 4px;
  line-height: 1.4em;
  margin: 0; }

h1 {
  font-size: 3em;
  letter-spacing: 20px;
  text-transform: uppercase;
  line-height: 1.5em;
  margin-bottom: 1em; }

h2 {
  font-size: 2.5em;
  letter-spacing: .5em;
  line-height: 2em;
  text-transform: uppercase; }

h3 {
  font-size: 2em;
  font-weight: 600;
  text-transform: uppercase; }

h4 {
  font-size: 1.5em; }

small {
  font-size: .75em; }

address {
  display: inline;
  font-style: normal; }

a {
  border-bottom: 1px solid;
  color: #000;
  padding: 2px;
  text-decoration: none;
  &:hover {
    text-decoration-color: initial; } }

ul,
p {
  font-family: Lora, serif; }

li {
  font-size: 1.25em;
  line-height: 1.4em;
  margin: .75em 0;
  padding-left: .5em; }

ul,
li {
  text-align: left; }

ul {
  margin: 0;
  padding-inline-start: 1em; }

p {
  font-size: 1.3em;
  line-height: 1.5em; }

header {
  align-items: center;
  background: transparent;
  display: flex;
  justify-content: space-between;
  position: absolute;
  text-align: center;
  width: 100%;
  z-index: 1; }
